@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
:root {
    --malachite-green: #61e786;
    --honey-dew: #edffec;
    --pineapple: #48435c;
}

body {
    padding: 0;
    margin: 0;
    font-family: 'Roboto Mono', monospace;
}
.app {
    background: var(--pineapple);
    height: 100vh;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.container {
    display: flex;
    flex-flow: column nowrap;
    width: 90vw;
    height: 100vh;
    background: rgba(125, 17, 40, .7);
    color: #f1f7ee;
    justify-content: center;
    align-items: center;
}

.content {
    height: 90%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

@media (min-width: 320px) and (max-width: 480px) {
    .app {
        flex-direction: row;
    }

    .container {
        width: 100vh;
        height: 90vh;
    }

}
.header {
    justify-content: center;
    align-items: center;
    background: rgba(125, 17, 40, .7);
    height: 100vh;
    width: 10vw;
    display: flex;
    justify-content: center;
}

.header-text {
    display: flex;
    flex-flow: column;
    justify-content: center;
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
}

.header-text span {
    height: 50px;
    width: 50px;
    border: 2px solid #ffffff;
    color: #61e786;
    background-color: rgba(255, 255, 255, .3);
}

.header-text span:nth-child(1),
.header-text span:nth-child(6) {
    color: rgb(125, 17, 40);
    margin-top: 1px;
    background-color: rgba(255, 255, 255, .9);
}

.header-text span:nth-child(odd) {
    -webkit-transform: skew(-15deg);
            transform: skew(-15deg);
}

.header-text span:nth-child(even) {
    -webkit-transform: skew(15deg);
            transform: skew(15deg);
}

@media (min-width: 320px) and (max-width: 480px) {
    .header {
        width: 100%;
        height: 10vh;
    }

    .header-text {
        flex-direction: row;
        font-size: 1.4rem;
        line-height: 23px;
    }

    .header-text span {
        height: 30px;
        width: 27px;
        border: 1px solid #ffffff;
    }

    .header-text span:nth-child(odd) {
        -webkit-transform: skewY(-15deg);
                transform: skewY(-15deg);
    }

    .header-text span:nth-child(even) {
        -webkit-transform: skewY(15deg);
                transform: skewY(15deg);
    }
}
.footer {
    height: 8%;
    width: 100%;
    text-align: center;
}

.footer span {
    margin: 0 25px;
}

.footer a {
    text-decoration: none;
    color: #61e786;
    font-weight: 600;
}

@media (min-width:320px) and (max-width:480px) {
    .footer span {
        display: block;
        font-size: 0.8rem;
    }
}
.quote {
    width: 60vw;
    display: flex;
    flex-direction: column;

}

.quote-author {
    align-self: flex-end;
    font-weight: bold;
}

.fa-quote-left {
    height: 4rem;
    color: #61e786;
}

.quote-text {
    font-size: 2rem;
}

@media (min-width: 320px) and (max-width: 480px) {
    .quote {
        width: 80vw;

    }

    .quote-text {
        font-size: 1.4rem;
    }
}
.btn {
    color: #ffffff;
    font-size: 1.2rem;
    font-weight: 600;
    text-align: center;
    text-decoration: none;
    padding: 10px 15px;
    margin: 20px;
    box-sizing: border-box;
    min-width: 150px;
}

.btn-group {
    display: flex;
    justify-content: center;
}

@media (max-width:480px) and (min-width:360px) {
    .btn {
        margin: 10px;
        min-width: 120px;
    }
}
.btn-tweet {
    display: inline-block;
    background-color: #0c85d0;
    border: 1px solid #ffffff;
}

.btn-tweet:hover {
    background-color: #0d95e8;
}
.btn-quote {
    color: var(--pineapple);
    background-color: var(--malachite-green);
    border: 1px solid #ffffff;
    padding: 12px 21px;
    cursor: pointer;
}

.btn-quote:hover {
    background-color: #35e064;
}
